import React, { Component } from 'react';
import dani from './../assets/Dani.mp4'
import Glagolica from "./Glagolica";


class Video extends Component {

    constructor(props){
        super(props);

        this.state = {
            showVideo: true
        }
    }

    onEnd = () => {
        this.setState({
            showVideo: false
        })
    };

    render() {
        return <div className='fade-enter fade-enter-active'>
            {this.state.showVideo && <video src={dani} className='video' autoPlay onEnded={this.onEnd}>
            </video> }
            {!this.state.showVideo && <Glagolica/>}
            </div>;
    }
}

export default Video;


